

.App{ margin-bottom: 50px;}
.card{
  -webkit-box-shadow: 0 3px 13px 1px rgba(0,0,0,0.12);
          box-shadow: 0 3px 13px 1px rgba(0,0,0,0.12);
}
.buttons{
  max-width: 38em;
  color:rgb(141, 141, 141);
  margin-top: 2em;
}

.badge{
  margin-bottom: 1em;
}

.image-limit{
  width:300px;
  height:100px;
}

.headshot{
  width:0px;
  visibility:hidden;
}

.bio{
  position:relative;
  width:auto;
  height: 400px;
  left: 50%;
  margin-left:-150px;
}

.headshot-text{
  position:absolute;
  z-index: 1;
}

.card-text{
  text-align: justify;
}

.holder{
  height:450px;
}
.holder{
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#f8b195+0,f67280+37,f67280+37,c06084+69,6c5b7b+95,355c7d+100 */
  background: #f8b195; /* Old browsers */ /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, #f8b195 0%, #f67280 37%,#c06084 69%,#6c5b7b 95%,#355c7d 100%); /* Chrome10-25,Safari5.1-6 */
  background: -webkit-linear-gradient(350deg, rgb(248, 177, 149) 0%,rgb(246, 114, 128) 37%,rgb(192, 96, 132) 69%,rgb(108, 91, 123) 95%,rgb(53, 92, 125) 100%);
  background: -o-linear-gradient(350deg, rgb(248, 177, 149) 0%,rgb(246, 114, 128) 37%,rgb(192, 96, 132) 69%,rgb(108, 91, 123) 95%,rgb(53, 92, 125) 100%);
  background: linear-gradient(100deg, rgb(248, 177, 149) 0%,rgb(246, 114, 128) 37%,rgb(192, 96, 132) 69%,rgb(108, 91, 123) 95%,rgb(53, 92, 125) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f8b195', endColorstr='#355c7d',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
  background-size: cover;
  z-index:-100;
}
.container-fluid{
  padding-right: 0px;
  padding-left: 0px;
}


.card-block{
  padding: .7em .7em .7em;
}

.card{
  border: none;
  border-radius: 0;
}

.Stack{
  margin-top: 20px !important;
}

img{
  border-radius: 0!important;
}

h1.card-title{
  font-size: 4.5em;
  font-weight:bold;
}

.btn-primary {
  border-width: 2px !important;
  border-style: solid;
  border-color: rgb(230, 230, 230);
  background-color: white;
  color: rgb(141, 141, 141) !important;
  border-radius: 30px;
  margin: .5em .5em .5em;
}
.btn-primary:hover{
  background-color: #404040;
  color: white !important;
  border-color:	#404040 !important;
}
.btn:focus,.btn:active {
   outline: none !important;
   -webkit-box-shadow: none !important;
           box-shadow: none !important;
}
.btn-primary:active{
  background-color: #404040 !important;
  border-color:	#404040 !important;
}
.more{
  background:rgba(0,0,0,0);
}
.card-block .btn{
  margin: 0;
}

span.projects{
  background-color:#f8b195;
}
a.projects{
  border-color:#f8b195 !important;
}
span.research{
  background:#F67280;
}
a.research{
  border-color:#F67280 !important;
}
span.publications{
  background:#C06084;
}
a.publications{
  border-color:#C06084 !important;
}
span.awards{
  background:#6C5B7B;
}
a.awards{
  border-color:#6C5B7B !important;
}
span.media{
  background:#355C7D;
  display: inline-block;
}
a.media{
  border-color:#355C7D !important;
  display: inline-block;
}
a.all{
  border-color:	#808080 !important;
}
.modal-body p{
  text-align: justify;
}
@media (min-width: 300px) {
  .bio{
    width:300px;
  }
 }

@media (min-width: 500px) {
  h1.card-title{
    font-size: 43px;
  }
  .headshot{
    visibility: visible;
    width:500px;
    position:absolute;
    margin-left:-100px;
    margin-top:-326px;
    z-index: 0;
  }

  .holder{
    height:580px;
    z-index: -1; /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg, #f8b195 0%, #f67280 37%,#c06084 69%,#6c5b7b 95%,#355c7d 100%); /* Chrome10-25,Safari5.1-6 */
    background: -webkit-linear-gradient(350deg, rgba(248, 177, 149, .6) 0%,rgba(246, 114, 128, .6) 37%,rgba(192, 96, 132, .6) 69%,rgba(108, 91, 123, .6) 95%,rgba(53, 92, 125, .6) 100%), url("./assets/tile.png");
    background: -o-linear-gradient(350deg, rgba(248, 177, 149, .6) 0%,rgba(246, 114, 128, .6) 37%,rgba(192, 96, 132, .6) 69%,rgba(108, 91, 123, .6) 95%,rgba(53, 92, 125, .6) 100%), url("./assets/tile.png");
    background: linear-gradient(100deg, rgba(248, 177, 149, .6) 0%,rgba(246, 114, 128, .6) 37%,rgba(192, 96, 132, .6) 69%,rgba(108, 91, 123, .6) 95%,rgba(53, 92, 125, .6) 100%), url("./assets/tile.png"); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  }

  .bio{
    padding-top: 285px;
    padding-bottom:50px;
    color:white;
  }

  .headshot-text{
    position:absolute;
    z-index: 1;
  }
 }


@media (min-width: 768px) {
  .bio{
    left: 40%;
    margin-left:-150px;
  }
 }


@media (min-width: 992px) {
